import React, { lazy, Suspense } from "react";
import { Routes, Route } from "react-router-dom";
import "swiper/scss";
import "swiper/css/bundle";
import { AuthProvider } from "contexts/auth-context";
import { ShopeeProvider } from "contexts/shopeeContext";


const SignUpPage = lazy(() => import("./pages/SignUpPage"));
const SignInPage = lazy(() => import("./pages/SignInPage"));
const ShopeePage = lazy(() => import("./pages/ShopeePage"));
const DetailsPage = lazy(() => import("./pages/DetailsPage"));
const PaymentPage = lazy(() => import("./pages/PaymentPage"));
const LayoutShopee = lazy(() => import("./layout/LayoutShopee"));


const App = () => {
  return (
    <AuthProvider>
      <ShopeeProvider>
        <Suspense>
          <Routes>
            {/* OutLet */}
            <Route element={<LayoutShopee></LayoutShopee>}>
              <Route path="/" element={<ShopeePage></ShopeePage>}></Route>
              <Route
                path="/:slug"
                element={<DetailsPage></DetailsPage>}
              ></Route>
            </Route>
            {/* ----- */}
            <Route
              path="/payment"
              element={<PaymentPage></PaymentPage>}
            ></Route>
            <Route path="/sign-up" element={<SignUpPage></SignUpPage>}></Route>
            <Route path="/sign-in" element={<SignInPage></SignInPage>}></Route>
           
          </Routes>
        </Suspense>
      </ShopeeProvider>
    </AuthProvider>
  );
};

export default App;
