import { FakeDataShopee } from "data/FakeDataShopee";
import useLocalStorage from "hooks/useLocalStorage";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { useAuth } from "./auth-context";
const { useContext } = require("react");
const { useState } = require("react");
const { createContext } = require("react");

const ShopeeContext = createContext();

function ShopeeProvider(props) {
  const { userInfo } = useAuth();

  const navigate = useNavigate();
  const fakeData = FakeDataShopee;

  const { storeedValue, setValue } = useLocalStorage("items", fakeData);
  const { storedValue: storedCart, setValue: setStoredCart } = useLocalStorage(
    "CartItems",
    []
  );

  const [cartItems, setCartItems] = useState(storedCart);

  const [checkAll, setCheckAll] = useState(false);

  const [totalMoney, setTotalMoney] = useState(0);

  const [bought, setBought] = useState([]);

  function hanleCheckAll() {
    setCheckAll(!checkAll);

    if (checkAll === false) {
      const result = cartItems.reduce((total, item) => {
        return total + item.price;
      }, 0);
      setTotalMoney(result);
    } else {
      setTotalMoney(0);
    }
  }
  function buyNow(newItem) {
    if (userInfo?.email) {
      setCartItems((prevItems) => {
        const isExisted = prevItems.some((item) => {
          return item.id === newItem.id;
        });
        if (isExisted) {
          setStoredCart([...prevItems]);
          return [...prevItems];
        }

        setStoredCart([...prevItems, newItem]);
        return [...prevItems, newItem];
      });
      navigate("payment");
    } else {
      navigate("/sign-in");
    }
  }

  function addToCart(newItem) {
    if (userInfo?.email) {
      setCartItems((prevItems) => {
        const isExisted = prevItems.some((item) => {
          return item.id === newItem.id;
        });
        if (isExisted) {
          setStoredCart([...prevItems]);
          return [...prevItems];
        }

        setStoredCart([...prevItems, newItem]);
        toast.success("Thm vo gi hng thnh cng", { autoClose: 500 });
        return [...prevItems, newItem];
      });
    } else {
      navigate("/sign-in");
    }
  }

  function removeCart(cartId) {
    setCartItems((prevItems) => {
      const result = prevItems.filter((item) => item.id !== cartId);
      setStoredCart(result);
      return result;
    });
  }

  function removeCartAll(cartId) {
    if (checkAll === true) {
      setCartItems((prevItems) => {
        const result = prevItems.filter((item) => item.id === cartId);
        setStoredCart(result);
        return result;
      });
    }
  }

  function buyItem() {
    if (checkAll === true) {
      const result = cartItems.map((item) => {
        return item;
      });
      setBought(result);
      toast.success("Mua hng thnh cng", { autoClose: 1000 });
      setCartItems([]);
      setStoredCart([]);
    }
  }
  const value = {
    addToCart,
    cartItems,
    checkAll,
    setCheckAll,
    totalMoney,
    removeCart,
    removeCartAll,
    hanleCheckAll,
    buyItem,
    buyNow,
  };

  return (
    <ShopeeContext.Provider value={value} {...props}></ShopeeContext.Provider>
  );
}

function useShopee() {
  const context = useContext(ShopeeContext);
  if (typeof context === "undefined")
    throw new Error("useShopee must be used within a ShopeeProvider");
  return context;
}
export { ShopeeProvider, useShopee };
